2px);\r\n  color: #000;\r\n  }\r\n","//Les mixines\r\n@mixin body-position {\r\n  max-width: $wmax;\r\n  min-width: $wmin;\r\n  margin: auto;\r\n}\r\n\r\n@mixin linear-gradient {\r\n  background: linear-gradient(rgba(255, 255, 255, 0) 0%, #00000080);\r\n}\r\n\r\n@mixin WH-full {\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n@mixin font-right($size) {\r\n  font-size: $size;\r\n  font-style: normal;\r\n  font-weight: 500;\r\n  text-align: right;\r\n  line-height: 142.6%;\r\n}\r\n\r\n@mixin font($size, $weight) {\r\n  font-size: ($size);\r\n  font-style: normal;\r\n  font-weight: $weight;\r\n  line-height: 142.6%;\r\n}\r\n\r\n@mixin bkgrd($background) {\r\n  background: $background;\r\n  background-blend-mode: darken;\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n}\r\n\r\n@mixin flex-center {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  text-align: center;\r\n}\r\n\r\n@mixin flex-column {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n}\r\n\r\n\r\n@mixin flex-end {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: flex-end;\r\n}\r\n\r\n@mixin flex-banner {\r\n  position: relative;\r\n  @include flex-center;\r\n  width: 100%;\r\n  @include bkgrd($banner-background);\r\n  margin-top: 20px;\r\n}\r\n\r\n@mixin rightArrow-style {\r\n  display: flex;\r\n  position: absolute;\r\n  font-size: 1.5rem;\r\n  top: 42%;\r\n  right: 2%;\r\n  width: 12px;\r\n  height: 20px;\r\n  z-index: 10;\r\n  cursor: pointer;\r\n  color: $tertiary-color;\r\n\r\n  @media screen and (min-width: $breakpoint-desktop) {\r\n    width: 47px;\r\n    height: 79px;\r\n    top: 42%;\r\n    font-size: 4.5rem;\r\n  }\r\n}\r\n\r\n@mixin leftArrow-style {\r\n  display: flex;\r\n  position: absolute;\r\n  font-size: 1.5rem;\r\n  top: 42%;\r\n  left: 2%;\r\n  height: 20px;\r\n  width: 12px;\r\n  z-index: 10;\r\n  cursor: pointer;\r\n  color: $tertiary-color;\r\n\r\n  @media screen and (min-width: $breakpoint-desktop) {\r\n    width: 47px;\r\n    height: 79px;\r\n    top: 42%;\r\n    font-size: 4.5rem;\r\n  }\r\n}\r\n\r\n@mixin media-query($key) {\r\n  $size: map-get($breakpoints, $key);\r\n  @media only screen and (min-width: $size) {\r\n    @content;\r\n  }\r\n}\r\n","//Les variables\r\n\r\n$primary-color: #ff6060;\r\n$secondary-color: #f7f7f7;\r\n$tertiary-color: #ffffff;\r\n$footer-color: #000000;\r\n$background-color: rgba(0, 0, 0, 0.3);\r\n$linear-cards: linear-gradient(\r\n  180deg,\r\n  rgba(255, 255, 255, 0) 0%,\r\n  rgba(0, 0, 0, 0.5) 100%\r\n);\r\n//les images\r\n$about-background: $background-color\r\n  url(../../assets/images/banner/aboutBanner.png);\r\n$about-background-mobile: $background-color\r\n  url(../../assets/images/banner/backgroundbannermobile.png);\r\n$banner-background: $background-color\r\n  url(../../assets/images/banner/homeBanner.png);\r\n\r\n$breakpoints: (\r\n  small: 350px,\r\n  medium: 768px,\r\n  large: 992px,\r\n  xlarge: 1200px,\r\n);\r\n\r\n$breakpoint-desktop: 769px;\r\n\r\n$wmin: 375px;\r\n$wmax: 768px;\r\n","%flex-column {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n%flex-end {\r\n  display: flex;\r\n  align-items: flex-end;\r\n  justify-content: center;\r\n}\r\n%flex-center {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n%flex-between {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n%flex-rowCenter {\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n%flex-rowBetween {\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n}\r\n%flex-rowAround {\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-around;\r\n}\r\n%flex-rowReverse {\r\n  display: flex;\r\n  flex-direction: row-reverse;\r\n  justify-content: space-between;\r\n}\r\n",".collapse {\r\n  display: flex;\r\n  width: 100%;\r\n  margin: 20px 0;\r\n  justify-content: center;\r\n  align-items: center;\r\n  &__dropdown {\r\n    width: 100%;\r\n\r\n    @media screen and (min-width: $breakpoint-desktop) {\r\n      width: 80%;\r\n    }\r\n\r\n    &__title {\r\n      @extend %flex-between;\r\n      padding: 0.5em 1em;\r\n      width: 100%;\r\n      background-color: $primary-color;\r\n      margin: 0 auto;\r\n      margin-top: 1.5em;\r\n      border-radius: 5px;\r\n      border: none;\r\n      cursor: pointer;\r\n      @media screen and (min-width: $breakpoint-desktop) {\r\n        width: 100%;\r\n      }\r\n\r\n      h2 {\r\n        font-family: \"Montserrat\", sans-serif;\r\n        font-size: 16px;\r\n        font-weight:700px;\r\n        color: $tertiary-color;\r\n      }\r\n      .fa-chevron-down {\r\n        color: $tertiary-color;\r\n      }\r\n      .fa-chevron-up {\r\n        color: $tertiary-color;\r\n      }\r\n    }\r\n    &__title:active .fa-chevron-down {\r\n      transform: rotate(180deg);\r\n      transition: transform 0.3s ease-in-out;\r\n    }\r\n    &__title:active .fa-chevron-up {\r\n      transform: rotate(-180deg);\r\n      transition: transform 0.3s ease-in-out;\r\n    }\r\n    &__content {\r\n      background-color: $secondary-color;\r\n      width: 100%;\r\n      border-radius: 5px;\r\n\r\n      @media screen and (min-width: $breakpoint-desktop) {\r\n        width: 100%;\r\n      }\r\n\r\n      p {\r\n        @include font(14px, 400);\r\n        font-family: \"Montserrat\", sans-serif;\r\n        color: #000000;\r\n        margin: 0 auto;\r\n        padding: 0.8em;\r\n        transition: transform 7s ease-in-out;\r\n      }\r\n    }\r\n  }\r\n}\r\n",".nav-header {\r\n  @include flex-end;\r\n\r\n  @media screen and (min-width: $breakpoint-desktop) {\r\n    text-transform: none;\r\n  }\r\n  \r\n  &__nav-link-home,\r\n  &__nav-link-about {\r\n    font-family: \"Montserrat\", sans-serif;\r\n    color: #000;\r\n    text-decoration: none;\r\n    padding-left